.like-button {
  padding: 0;
  margin: 0;
  width: 22px;
  height: 19px;
  background-image: url(../../images/LikeButton.svg);
  background-size: cover;
  border: none;
  background-color: rgb(0,0,0,0);
  margin-right: 15px;
}
.like-button:hover {
  opacity: 0.5;
  cursor: pointer;
}

